@font-face {
  font-family: 'Montserrat';
  src: URL('./fonts/Montserrat-Light.ttf') format('truetype');
}

body {
  background: #123;
  color: #FED;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

main {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: flex-start;
  justify-content: flex-start;
  flex-flow: column;
  font-family: 'Montserrat', Tahoma, Geneva, Verdana, sans-serif;
  p { text-align: center;}
  background-size: cover;
  min-height: 100vh;
  height: 100vh;
  margin: 0;

  .hover-title {
    position: absolute;
    z-index: 100;
    width: 100%;
    text-align: center;
    background-color: rgba(0,0,0,.3);
    top: -1em;
    left: 0;
    pointer-events: none;
    user-select: none;
    padding: 1em;
    font-size: 1.2em;
  }

  .logo {
    width: 20vw;
    height: 15%;
    min-width: 86px;
    margin: 2.7vw;
    z-index: 1000;
    pointer-events: none;
  }

  .oe-logo {
    width: 20.64vw;
    height: 5.4vw;
    min-width: 86px;
    min-height: 22.5px;
    max-width: 688px / 4;
    max-height: 180px / 4;
    position: fixed;
    bottom: 2.7vw;
    right: 2.7vw;
    pointer-events: none;
    user-select: none;
  }
  

  .welcome-message, .how-to-use p {
    width: 22em;
    font-size: calc(.80vw + 4px);
    color: rgb(151, 152, 218);
    padding: 1em 1em 1em 2.7vw;
    align-self: flex-start;
    position: relative;
    user-select: none;
    background-color: rgb(0, 0, 0, 0.5);

    p { width: 100%; text-align: left; }
  }
}

.how-to-use {
  position: fixed;
  top: 2.7vw;
  right: -2.7vw;
  margin: 0;
  padding: 1em 2.7vw 1em 1em;
  p {
    color: rgb(152, 127, 197)!important;
    text-align: left;
  }
}

.debugger {
  position: absolute;
  display: flex;
  justify-items: center;
  align-items: center;
  align-content: center;
  top: 0;
  left: 0;
  width: auto;
  height: 3em;
  background: #34A;
  pointer-events: none;
  user-select: none;
  transform: translate(-30px, -40px);
  z-index: 50;
  border-radius: .3em;
  font-family: 'Montserrat', Tahoma, Geneva, Verdana, sans-serif;
  
  span {
    padding: 0 1em;
    width: 100%;
    text-align: center;
  }
}

.no-pointer { pointer-events: none; }

.full-screen {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 1;
}

circle {
  cursor: move;
}

.base-color { fill: royalblue;}
._23A {fill: rgb(17, 74, 128);}
._765 {fill: rgb(121, 64, 0);}
._A75 {fill: rgb(37, 47, 70);}
._A34 {fill: rgb(165, 13, 84);}
._782 {fill: rgb(6, 76, 78);}
._11F {fill: rgb(117, 15, 88);}
._927 {fill: rgb(48, 131, 76);}
._72A {fill: rgb(76, 18, 110);}
._FED {fill: rgb(214, 83, 7);}
._123 {fill: rgb(78, 13, 46);}
._888 {fill: rgb(42, 51, 3);}

.off {display: none;}

[data-opened="true"] {
  stroke-dasharray: 2,3;
}

.eu-955402 {
  position: absolute;
  display: flex;
  align-items: center;
  align-content: center;
  bottom: 2.7vw;
  left: 2.7vw;
  height: 5.4vw;

  pointer-events: none;
  img { height: 5.4vw; user-select: none;}
  .vertical-center {
    height: 5.4vw;
    display: flex;
    align-items: center;
    background-color: rgb(0, 0, 0, 0.5);
  }
  p { 
    margin: 1em; 
    font-size: calc(.90vw + 1px);
    color: rgb(143, 201, 255);
    text-align: left;
    min-width: 20vw;
    max-width: 30vw;
    @media only screen and (max-width: 600px) {
      max-width: 60vw;
      font-size: 1.5vw;
    }
    // height: calc(5.4vw - .6em);
    padding: .3em;
    user-select: none;
  }
}

menu {
  display: block;
  position: absolute;
  top: 0;
  margin: 0;
  right: 0;
  height: 100vh;
  z-index: 2200;
  width: 40vw;
  @media only screen and (max-width: 600px) {
    width: 100vw;
  }
  overflow-x: hidden;
  overflow-y: scroll;
  
  .menu-item {
    padding: .5em;
    box-shadow: 0 1px 0 #BBB4 inset, 0 -1px 0 #4444 inset;
    &[data-group="3"]:hover {filter: brightness(1.3);} 
    a {
      text-decoration: none;
      color: #FED;
      &:visited {
        color: #DBA;
      }
    }
    &[data-group="2"] {
      padding-left: 2em; 
      &[data-color="#23A"] {background-color: rgb(17, 74, 128);}
      &[data-color="#765"] {background-color: rgb(121, 64, 0);}
      &[data-color="#A75"] {background-color: rgb(37, 47, 70);}
      &[data-color="#A34"] {background-color: rgb(165, 13, 84);}
      &[data-color="#782"] {background-color: rgb(6, 76, 78);}
      &[data-color="#11F"] {background-color: rgb(117, 15, 88);}
      &[data-color="#927"] {background-color: rgb(48, 131, 76);}
      &[data-color="#72A"] {background-color: rgb(76, 18, 110);}
      &[data-color="#FED"] {background-color: rgb(214, 83, 7);}
      &[data-color="#123"] {background-color: rgb(78, 13, 46);}
      &[data-color="#888"] {background-color: rgb(42, 51, 3);}      
    }
    &[data-group="1"] {
      padding-left: 1em; 
      &[data-color="#23A"] {background-color: darken(rgb(17, 74, 128), 5%);}
      &[data-color="#765"] {background-color: darken(rgb(121, 64, 0), 5%);}
      &[data-color="#A75"] {background-color: darken(rgb(37, 47, 70), 5%);}
      &[data-color="#A34"] {background-color: darken(rgb(165, 13, 84), 5%);}
      &[data-color="#782"] {background-color: darken(rgb(6, 76, 78), 5%);}
      &[data-color="#11F"] {background-color: darken(rgb(117, 15, 88), 5%);}
      &[data-color="#927"] {background-color: darken(rgb(48, 131, 76), 5%);}
      &[data-color="#72A"] {background-color: darken(rgb(76, 18, 110), 5%);}
      &[data-color="#FED"] {background-color: darken(rgb(214, 83, 7), 5%);}
      &[data-color="#123"] {background-color: darken(rgb(78, 13, 46), 5%);}
      &[data-color="#888"] {background-color: darken(rgb(42, 51, 3), 5%);}      
    }
    &[data-group="3"] {
      padding-left: 3em; 
      &[data-color="#23A"] {background-color: lighten(rgb(17, 74, 128), 15%);}
      &[data-color="#765"] {background-color: lighten(rgb(121, 64, 0), 15%);}
      &[data-color="#A75"] {background-color: lighten(rgb(37, 47, 70), 15%);}
      &[data-color="#A34"] {background-color: lighten(rgb(165, 13, 84), 15%);}
      &[data-color="#782"] {background-color: lighten(rgb(6, 76, 78), 15%);}
      &[data-color="#11F"] {background-color: lighten(rgb(117, 15, 88), 15%);}
      &[data-color="#927"] {background-color: lighten(rgb(48, 131, 76), 15%);}
      &[data-color="#72A"] {background-color: lighten(rgb(76, 18, 110), 15%);}
      &[data-color="#FED"] {background-color: lighten(rgb(214, 83, 7), 15%);}
      &[data-color="#123"] {background-color: lighten(rgb(78, 13, 46), 15%);}
      &[data-color="#888"] {background-color: lighten(rgb(42, 51, 3), 15%);}      
    }

  }
}


.menu-switch {
  display: block;
  position: fixed;
  top: 9px;
  right: 9px;
  width: 34px;
  height: 34px;
  z-index: 3000;
  pointer-events: auto;
  cursor: pointer;
}
